.myBtn {
    padding: 5px 15px;
    color: teal;
    font-size: 14px;
    background: transparent;
    border: 1px solid teal;
    cursor: pointer;
    transition: background-color 0.3s ease-out;
    width: 100%;
    height: 40px;
}

.myBtn:hover {
    color: white;
    background-color: teal;
}